input,
textarea {
	&.form-control {
		@include transition(all 0.15s ease-in-out);
		border-radius: 0;
		border: 1px solid $input-border-color;
		padding: 10px 15px;
		height: auto;
		&:focus {
			@include box-shadow(none);
			border-color: $input-focus-color;
		}
	}
}

textarea {
	max-width: 100%;
	min-height: 150px;
}

label {
	color: $label-color;
	&.required {
		&::after {
			color: $input-required-color;
			content: '*';
			font-size: 15px;
			padding-left: 5px;
			display: inline-block;
			line-height: 15px;
		}
	}
}

.infobox {
	&-danger {
		@include transition(all 0.25s ease-in-out);
		background: $infobox-color-warning;
		color: $white;
		padding: 10px 15px;
		margin-top: 30px;
	}
}
